.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hiding-button {
  animation: fadeIn 0.5s;
  opacity: 1.0;
}

.hiding-button.hide {
  opacity: 0.0;
  animation: fadeOut 0.5s;
}

@keyframes fadeOut {
  from {
    opacity: 1.0;
  }

  to {
    opacity: 0.0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0.0;
  }

  to {
    opacity: 1.0;
  }
}

.waiting-for-deletion {
  opacity: 0.5;
  /* animation: fadeOut; */
}

